import React, { useState, useEffect } from 'react';
import styles from './WordRow.module.scss';

export default function WordRow({ word, onSave, onDelete }) {
  const [isEditing, setIsEditing] = useState(false);
  const [edited, setEdited]       = useState(word);

  const englishInvalid       = isEditing && !edited.english.trim();
  const transcriptionInvalid = isEditing && !edited.transcription.trim();
  const russianInvalid       = isEditing && !edited.russian.trim();
  const tagsInvalid          = isEditing && !(edited.tags?.trim());

  // –ø—Ä–∏ –≤—Ö–æ–¥–µ –≤ —Ä–µ–∂–∏–º —Ä–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞–Ω–∏—è –∫–æ–ø–∏—Ä—É–µ–º —Ç–µ–∫—É—â–µ–µ —Å–ª–æ–≤–æ
  useEffect(() => {
    if (isEditing) setEdited(word);
  }, [isEditing, word]);

  // –æ–±–Ω–æ–≤–ª—è–µ–º —Å–æ—Å—Ç–æ—è–Ω–∏–µ –ø—Ä–∏ –≤–≤–æ–¥–µ
  const handleChange = e => {
    const { name, value } = e.target;
    setEdited(prev => ({ ...prev, [name]: value }));
  };

  // —Å–æ—Ö—Ä–∞–Ω–∏—Ç—å
  const handleSave = () => {
    if (englishInvalid || transcriptionInvalid || russianInvalid) {
      alert('–û—à–∏–±–∫–∞: –∑–∞–ø–æ–ª–Ω–∏—Ç–µ –≤—Å–µ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω—ã–µ –ø–æ–ª—è –ø–µ—Ä–µ–¥ —Å–æ—Ö—Ä–∞–Ω–µ–Ω–∏–µ–º');
      return;
    }
    console.log('–°–æ—Ö—Ä–∞–Ω–µ–Ω–æ —Å–ª–æ–≤–æ:', edited);
    onSave(edited);
    setIsEditing(false);
  };

  // –æ—Ç–º–µ–Ω–∏—Ç—å
  const handleCancel = () => {
    setIsEditing(false);
    setEdited(word);
  };

  // Enter = —Å–æ—Ö—Ä–∞–Ω–∏—Ç—å
  const handleKeyDown = e => {
    if (e.key === 'Enter') {
      e.preventDefault();
      handleSave();
    }
  };

  return (
    <tr className={styles.row}>
      {isEditing ? (
        <>
          <td>
            <input
              name="english"
              value={edited.english}
              onChange={handleChange}
              onKeyDown={handleKeyDown}
              className={`${styles.input} ${englishInvalid ? styles.errorInput : ''}`}
            />
          </td>
          <td>
            <input
              name="transcription"
              value={edited.transcription}
              onChange={handleChange}
              onKeyDown={handleKeyDown}
              className={`${styles.input} ${transcriptionInvalid ? styles.errorInput : ''}`}
            />
          </td>
          <td>
            <input
              name="russian"
              value={edited.russian}
              onChange={handleChange}
              onKeyDown={handleKeyDown}
              className={`${styles.input} ${russianInvalid ? styles.errorInput : ''}`}
            />
          </td>
          <td>
            <input
              name="tags"
              value={edited.tags || ''}
              onChange={handleChange}
              onKeyDown={handleKeyDown}
              className={`${styles.input} ${tagsInvalid ? styles.errorInput : ''}`}
            />
          </td>
          <td className={styles.actions}>
            <button
              onClick={handleSave}
              className={styles.button}
              disabled={englishInvalid || transcriptionInvalid || russianInvalid}
            >
              üíæ
            </button>
            <button onClick={handleCancel} className={styles.button}>‚ùå</button>
          </td>
        </>
      ) : (
        <>
          <td>{word.english}</td>
          <td>{word.transcription}</td>
          <td>{word.russian}</td>
          <td>{word.tags}</td>
          <td className={styles.actions}>
            <button onClick={() => setIsEditing(true)} className={styles.button}>‚úèÔ∏è</button>
            <button onClick={onDelete} className={styles.button}>üóë</button>
          </td>
        </>
      )}
    </tr>
  );
}
